@import "../../../../../styles/variables";

:local {
  .chat {
    margin-top: 20px;
    background-color: $background-white;
    border-radius: $medium-border-radius;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 67%;
    width: 100%;
    padding: 10px;
  }

  .window {
    width: 100%;
    height: 80%;
    flex: 1 0 auto;
    list-style-type: none;
    padding: 0;
    overflow: auto;
    display: flex;
    flex-direction: column;
  }

  .button {
    min-width: 20px;
    flex-basis: 47px;
    background: $background-white-blue;
    height: 100%;
    box-sizing: border-box;
    border-radius: 10px;
    transition: background-color 0.3s ease;
  }

  .button:hover {
    background-color: $background-blue;
  }

  .nonActive {
    pointer-events: none;
    opacity: 0.5;
  }

  .inputBox {
    width: 100%;
    height: 47px;
    display: flex;
    padding: 4px;
    border-radius: $medium-border-radius;
    background-color: $background-white-blue;
  }
  .input {
    font-size: 1.2vw;
    flex-grow: 1;
    border-radius: $little-border-radius;
    padding-left: 5px;
  }

  .img {
    width: 30px;
    height: 30px;
    object-fit: contain;
  }
}
