@import "variables";

.btn {
  width: 166px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  border-radius: $little-border-radius;
  background-color: $background-white-blue;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: $background-blue;
}

.input {
  border: 5px solid $background-white-blue;
  font-size: 24px;
  padding: 5px;
  height: 60px;
  border-radius: $little-border-radius;
}
