@import "../../../../../styles/variables";

.message {
  margin: 10px 0 10px 0;
  width: calc(100% - 20px);
  white-space: initial;
  overflow-wrap: anywhere;
}

.name {
  color: $background-white-blue;
  font-size: 20px;
  margin-bottom: 5px;
  margin-left: 5px;
  font-weight: 600;
}

.text {
  background: $background-white;
  border: 5px solid $background-white-blue;
  display: flex;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
  border-radius: $medium-border-radius;
  min-height: 47px;
}

.button {
  flex-basis: 50%;
  margin-top: 2px;
  height: 20px;
  border-radius: 7px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
}

.buttons {
  display: flex;
}

.buttonRightAnswer {
  background-color: #00dc09;
  width: 100%;
  &:hover {
    background-color: #00ab06;
  }
}

.buttonLike {
  margin-left: 5px;
  background: $background-white-blue;
  &:hover {
    background: $background-blue;
  }
}

.buttonDislike {
  background: #ff441a;
  &:hover {
    background: #d24545;
  }
}

.positive {
  border-color: $background-white-blue;
  background-color: #b6cbfa;
}

.negative {
  border-color: #ff0000;
  background-color: #ffb0b0;
}

.neutral {
  background-color: #fbfbfb;
}

.right {
  border-color: #00ab06;
  background-color: #00dc09;
}
